.jumbo {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
    height: 800px;
    background-image: url(../img/jumbo.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
}

.jumbo-search {
    margin-top: 80px;
    width: 60%;
    height: 65px;
    background-color: #ffffff;
    border-radius: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.search-input {
    width: 100%;
    border-radius: 100px;
    border: 0px solid;
    outline: none;
    margin-left: 30px;
}

.search-btn {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-content: center;
    align-items: center;
    background-color: #62cb66;
    height: 50px;
    min-width: 50px;
    border-radius: 50px;
    margin: 10px;
    color: #ffffff;
    cursor: pointer;
}

