@media(max-width: 1600px) {
    .jumbo {
        height: 650px;
    }
}

@media(max-width: 1450px) {
    .jumbo {
        height: 600px;
    }
}

@media(max-width: 1000px) {
    .jumbo {
        height: 450px;
    }
}

@media(max-width: 850px) {
    .jumbo {
        height: 300px;
    }
}